import React from 'react';

import { Badge, Wrapper } from './SubscriptionPricingCard.styles';
import Props from './SubscriptionPricingCard.types';

const SubscriptionPricingCard: React.FC<Props> = ({
  children,
  badgeText,
  className,
  ...rest
}) => (
  <Wrapper className={className} {...rest}>
    {badgeText && <Badge>{badgeText}</Badge>}
    {children}
  </Wrapper>
);

export default SubscriptionPricingCard;
