import { styled } from 'styled-components';

import Capsule from '#components/Capsule';

import PricingDetails, {
  NormalDiscountedPricing,
  SubscriptionLength,
  ContinuedPrice,
  SubscriptionLengthWrapper,
} from '../PricingDetails';

import { CLASS_IS_SELECTED } from './WithDetailsAndRadioButton.constants';

const SMALL_TABLET_BREAKPOINT = '500px';

export const WithDetailsAndRadioButtonWrapper = styled.div`
  * {
    box-sizing: border-box;
  }
`;

export const Label = styled.label`
  --radio-indicator-size: 24px;
  --wrapper-padding: ${({ theme }) => theme.spacing.x2}px;
  display: flex;
  position: relative;
  height: 100%;
  flex-direction: column;
  padding: var(--wrapper-padding);
  padding-right: calc(
    ${({ theme }) => theme.spacing.x2}px + var(--radio-indicator-size)
  );
  border: 2px solid ${({ theme }) => theme.color.divider.light};
  border-radius: 8px;
  background-color: ${({ theme }) => theme.color.canvas.secondary};
  cursor: pointer;

  &.${CLASS_IS_SELECTED} {
    border-color: ${({ theme }) => theme.color.primary.base};
    background-color: ${({ theme }) => theme.color.canvas.card};
  }

  @media (min-width: ${SMALL_TABLET_BREAKPOINT}) {
    --wrapper-padding: ${({ theme }) => theme.spacing.x2}px
      ${({ theme }) => theme.spacing.x1}px ${({ theme }) => theme.spacing.x1}px;
    padding-right: ${({ theme }) => theme.spacing.x1}px;
  }
`;

export const RadioButton = styled.input.attrs({
  type: 'radio',
  name: 'subscription',
})`
  position: absolute;
  right: 0;
  opacity: 0;
  outline: none;

  &:focus + ${Label} {
    border-color: ${({ theme }) => theme.color.semiotic.info};
  }
`;

export const StyledPricingDetails = styled(PricingDetails)`
  ${SubscriptionLengthWrapper} {
    justify-content: flex-start;
    margin-bottom: ${({ theme }) => theme.spacing.x0_5}px;

    @media (min-width: ${SMALL_TABLET_BREAKPOINT}) {
      justify-content: center;
      margin-bottom: ${({ theme }) => theme.spacing.x1}px;
    }
  }
  ${SubscriptionLength} {
    margin: 0;
    font: ${({ theme }) =>
      theme.textStyle.subscriptionPricingCard.radioButtonVariant.pricingDetails
        .subscriptionLength.font};
    letter-spacing: ${({ theme }) =>
      theme.textStyle.subscriptionPricingCard.radioButtonVariant.pricingDetails
        .subscriptionLength.letterSpacing};
    text-transform: uppercase;

    @media (max-width: ${SMALL_TABLET_BREAKPOINT}) {
      position: relative;
      top: 1px;
    }
  }
  ${NormalDiscountedPricing} {
    margin-bottom: ${({ theme }) => theme.spacing.x0_5}px;
    font: ${({ theme }) =>
      theme.textStyle.subscriptionPricingCard.radioButtonVariant.pricingDetails
        .normalDiscountedPricing.font};
    letter-spacing: ${({ theme }) =>
      theme.textStyle.subscriptionPricingCard.radioButtonVariant.pricingDetails
        .normalDiscountedPricing.letterSpacing};
    text-align: left;

    @media (min-width: ${SMALL_TABLET_BREAKPOINT}) {
      margin-bottom: ${({ theme }) => theme.spacing.x1}px;
      text-align: center;
    }
  }
  ${ContinuedPrice} {
    margin-bottom: 0;
    font: ${({ theme }) =>
      theme.textStyle.subscriptionPricingCard.radioButtonVariant.pricingDetails
        .continuedPrice};
    text-align: left;

    @media (min-width: ${SMALL_TABLET_BREAKPOINT}) {
      margin-bottom: ${({ theme }) => theme.spacing.x1}px;
      text-align: center;
    }
  }
`;

export const StyledCapsule = styled(Capsule)`
  white-space: nowrap;

  @media (min-width: ${SMALL_TABLET_BREAKPOINT}) {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%, -50%);
  }
`;

export const RadioButtonIndicator = styled.span`
  display: grid;
  position: absolute;
  top: 50%;
  right: var(--wrapper-padding);
  width: var(--radio-indicator-size);
  height: var(--radio-indicator-size);
  border: 1px solid ${({ theme }) => theme.color.ink.muted};
  border-radius: 50%;
  place-items: center;
  transform: translateY(-50%);

  @media (min-width: ${SMALL_TABLET_BREAKPOINT}) {
    position: static;
    margin: auto auto 0;
    transform: none;
  }

  .${CLASS_IS_SELECTED} &::before {
    --size: 14px;
    display: block;
    width: var(--size);
    height: var(--size);
    border-radius: 50%;
    background-color: ${({ theme }) => theme.color.semiotic.success};
    content: '';
  }
`;
