import { FC } from 'react';

import { CLASS_IS_SELECTED } from './WithDetailsAndRadioButton.constants';
import {
  WithDetailsAndRadioButtonWrapper,
  RadioButton,
  Label,
  StyledCapsule,
  StyledPricingDetails,
  RadioButtonIndicator,
} from './WithDetailsAndRadioButton.styles';
import WithDetailsAndRadioButtonProps from './WithDetailsAndRadioButton.types';

const WithDetailsAndRadioButton: FC<WithDetailsAndRadioButtonProps> = ({
  isSelected = false,
  badgeText,
  subscriptionLength,
  discountedPrice,
  normalPrice,
  continuedPrice,
  inputId,
  inputValue,
  onChange,
  ...rest
}) => (
  <WithDetailsAndRadioButtonWrapper {...rest}>
    <RadioButton
      id={inputId}
      value={inputValue}
      checked={isSelected}
      onChange={onChange}
    />
    <Label
      htmlFor={inputId}
      {...(isSelected && { className: CLASS_IS_SELECTED })}
    >
      <StyledPricingDetails
        continuedPrice={continuedPrice}
        discountedPrice={discountedPrice}
        normalPrice={normalPrice}
        subscriptionLength={subscriptionLength}
      >
        {badgeText && <StyledCapsule>{badgeText}</StyledCapsule>}
      </StyledPricingDetails>
      <RadioButtonIndicator />
    </Label>
  </WithDetailsAndRadioButtonWrapper>
);

export default WithDetailsAndRadioButton;
