import React from 'react';

import PricingDetails from '../PricingDetails';

import { WithDetailsWrapper } from './SubscriptionPricingCardWithDetails.styles';
import Props from './SubscriptionPricingCardWithDetails.types';

const SubscriptionPricingCardWithDetails: React.FC<Props> = ({
  subscriptionLength,
  discountedPrice,
  normalPrice,
  continuedPrice,
  badgeText,
  children,
  ...rest
}) => (
  <WithDetailsWrapper badgeText={badgeText} {...rest}>
    <PricingDetails
      continuedPrice={continuedPrice}
      discountedPrice={discountedPrice}
      normalPrice={normalPrice}
      subscriptionLength={subscriptionLength}
    />
    {children}
  </WithDetailsWrapper>
);

export default SubscriptionPricingCardWithDetails;
